* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}
.hero {
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #6ac1c5, #bda5ff);
  position: relative;
}
#calendar {
  width: 90%;
  height: 90%;
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}
